// App.js
import React, { useState } from 'react';
import './App.css';
import Form from './components/Form';
import UserList from './components/UserList';
import EmbeddedDashboard from './components/EmbeddedDashboard';
import SmallFrame from './components/SmallFrame';
import GaussianGraph from './components/GaussianGraph';
import MyImage from './components/logotdtu.png';
//import GET from './components/GET';
function App() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [currentPage, setCurrentPage] = useState('form'); //  xc nh trang hin ti

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleMenuClick = (page) => {
    setCurrentPage(page);
    setIsMenuOpen(false); // ng menu khi chn mt trang
  };
  return (
    <div className="App">

      <header>
        <div className="menu-icon" onClick={toggleMenu}>
          <div className="menu-line" />
          <div className="menu-line" />
          <div className="menu-line" />
          <div className="menu-line" />
        </div>
        <h1 className="app-title">A SIMULATION SYSTEM OF HUMAN MOTION - THE HAND PART</h1>
        <div className="image-container">
          <img src={MyImage} alt="Avatar" className="avatar-image" />
        </div>
      </header>
      <main>
        <div className={`form-container ${currentPage === 'form' ? 'visible' : 'hidden'}`}>
          <Form />
        </div>
        <div className={`user-list-container ${currentPage === 'userList' ? 'visible' : 'hidden'}`}>
          <UserList />
        </div>
        <div className={`chartmongo-container ${currentPage === 'userList2' ? 'visible' : 'hidden'}`}>
          <EmbeddedDashboard />

        </div>
        {currentPage === 'PAGE4' && (
          <div className="user-list-container">
            <GaussianGraph />
            <SmallFrame />
          </div>

        )}

      </main>
      <aside className={`side-menu ${isMenuOpen ? 'open' : ''}`}>
        <ul>
          <li onClick={() => handleMenuClick('form')}>NHP THNG TIN</li>
          <li onClick={() => handleMenuClick('userList')}>DANH SCH NGI DNG</li>
          <li onClick={() => handleMenuClick('userList2')}>BIU  THNG S THEO THI GIAN</li> {/* Thm mc mi */}
          <li onClick={() => handleMenuClick('PAGE4')}>M PHNG CHUYN NG</li>
        </ul>
      </aside>
    </div>
  );
}

export default App;
