import React, { useState } from 'react';
import Form from '@rjsf/core';
import validator from '@rjsf/validator-ajv8';
import axios from 'axios';
import './UserLogin.css'; // Import file CSS ty chnh

const initialSchema = {
  title: 'MI NHP TI KHON',
  type: 'object',
  required: ['username', 'password', 'gender', 'height', 'weight', 'medicalHistory'],
  properties: {
    username: { type: 'string', title: 'TN BNH NHN' },
    password: { type: 'string', title: 'M S', format: 'password' },
    gender: { type: 'string', title: 'GII TNH', enum: ['Nam', 'N', 'Khc'] },
    height: { type: 'number', title: 'CHIU CAO (cm)' },
    weight: { type: 'number', title: 'CN NNG (kg)' },
    medicalHistory: { type: 'string', title: 'TIN S BNH', format: 'textarea' },
  },
};

const additionalSchema = {
  title: 'THNG TIN THM',
  type: 'object',
  required: ['heartRate', 'spo2', 'gripStrength'],
  properties: {
    heartRate: { type: 'number', title: 'Nhp tim' },
    spo2: { type: 'number', title: 'SpO2' },
    gripStrength: { type: 'number', title: 'Lc nm' },
  },
};

const UserLogin = () => {
  const [formData, setFormData] = useState({});
  const [showAdditionalForm, setShowAdditionalForm] = useState(false);
  const [submitCount, setSubmitCount] = useState(0);

  const handleSubmit = async ({ formData }) => {
    try {
      console.log('D liu gi i:', formData);

      const response = await axios.post(
        'https://ap-southeast-1.aws.data.mongodb-api.com/app/data-byptt/endpoint/POST_JSON_BACKEND',
        formData
      );

      console.log('Kt qu t server:', response.data);
      alert('D LIU  C LU LI CM N  BN  S DNG');
      // Reset form sau khi submit thnh cng
      setFormData({});
      setSubmitCount(submitCount + 1);

      // Hin th form mi sau khi gi d liu thnh cng
      setShowAdditionalForm(true);
    } catch (error) {
      console.error('Li khi gi d liu:', error);
    }
  };

  const handleAdditionalSubmit = async ({ formData }) => {
    try {
      console.log('D liu gi i (Additional):', formData);

      const response = await axios.post(
        'https://ap-southeast-1.aws.data.mongodb-api.com/app/data-tqlme/endpoint/POST_JSON_BACKEND',
        formData
      );

      console.log('Kt qu t server (Additional):', response.data);

      // Reset form sau khi submit thnh cng
      setShowAdditionalForm({});
      setFormData(true);
    } catch (error) {
      console.error('Li khi gi d liu (Additional):', error);
    }
  };







  return (
    <div className="auth-form-container">
      {submitCount % 2 === 0 ? (
        <Form
          schema={initialSchema}
          validator={validator}
          formData={formData}
          onChange={({ formData }) => setFormData(formData)}
          onSubmit={handleSubmit}
        />
      ) : (
        <Form
          schema={additionalSchema}
          validator={validator}
          formData={showAdditionalForm}
          onChange={({ formData }) => setShowAdditionalForm(formData)}
          onSubmit={handleAdditionalSubmit}
        />
      )}
    </div>
  );
};

export default UserLogin;